.banners {
  width: 100%;
  height: 240px;
  margin-bottom: 20px;
  background: url(../images/downcount-banner.png) no-repeat;
  background-size: 100% 100%;
  position: relative;
  border-radius: 10px;

  .title {
    position: absolute;
    height: 56px;
    left: 46px;
    top: 69px;
    font-weight: 600;
    font-size: 40px;
    line-height: 56px;
    color: #fff;

    &.titleEn {
      font-size: 30px;
    }
  }

  .desc {
    position: absolute;
    height: 35px;
    left: 49px;
    bottom: 68px;
    background: #8939e5;
    border-radius: 20px;
    line-height: 35px;
    color: #fff;
    text-align: center;
    font-size: 18px;
    padding: 0 20px;
  }

  .count {
    position: absolute;
    top: 68px;
    right: 50px;
    width: 362px;
    height: 85px;
  }

  .countdesc {
    position: absolute;
    top: 150px;
    right: 10px;
    width: 402px;
    height: 16x;

    span {
      display: inline-block;
      font-weight: normal;
      font-size: 12px;
      line-height: 16px;
      text-align: center;
      width: 94px;
      margin-right: 40px;
      color: #fff;
      opacity: 0.6;
    }
  }
}

@media screen and (max-width: 1400px) and (min-width: 1201px) {
  .banners {
    .title {
      &.titleEn {
        font-size: 30px;
        width: 300px;
        line-height: 30px;
        text-align: left;
      }
    }

    .desc {
      &.descEn {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1200px) and (min-width: 1001px) {
  .banners {
    .title {
      &.titleEn {
        font-size: 24px;
        width: 300px;
        line-height: 30px;
        text-align: left;
      }
    }

    .desc {
      &.descEn {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 1000px) and (min-width: 769px) {
  .banners {
    .title {
      position: absolute;
      height: 56px;
      left: 26px;
      top: 69px;
      font-weight: 600;
      font-size: 30px;
      line-height: 56px;
      color: #fff;

      &.titleEn {
        font-size: 24px;
        width: 300px;
        line-height: 30px;
        text-align: left;
      }
    }

    .desc {
      position: absolute;
      height: 30px;
      left: 29px;
      bottom: 68px;
      background: #8939e5;
      border-radius: 20px;
      line-height: 30px;
      color: #fff;
      text-align: center;
      font-size: 14px;

      &.descEn {
        font-size: 12px;
      }
    }

    .count {
      right: 20px;
    }

    .countdesc {
      right: -20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .banners {
    height: 200px;

    .title {
      position: absolute;
      height: 40px;
      width: 100%;
      top: 10px;
      left: 0;
      font-weight: 600;
      font-size: 30px;
      line-height: 40px;
      color: #fff;
      text-align: center;

      &.titleEn {
        font-size: 20px;
      }
    }

    .desc {
      position: absolute;
      width: 100%;
      height: 30px;
      left: 0;
      top: 60px;
      margin-left: 0;
      background: #8939e5;
      border-radius: 20px;
      line-height: 30px;
      color: #fff;
      text-align: center;
      font-size: 14px;
      padding: 0 10px;

      &.descEn {
        width: 100%;
        margin-left: 0;
        font-size: 10px;
      }
    }

    .count {
      width: 272px;
      top: 100px;
      left: 50%;
      margin-left: -136px;
    }

    .countdesc {
      top: 160px;
      left: 50%;
      margin-left: -136px;
      width: 300px;

      span {
        display: inline-block;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        width: 72px;
        margin-right: 28px;
        color: #fff;
        opacity: 0.6;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .banners {
    margin-top: 20px;
  }
}
